<template>
    <div style="margin-bottom: 55px;margin-top: 25px;">
        <!-- 特色榜 -->
		<!--Layout布局中的基础布局(Element ui)-->
		<el-row class="el-row1">
			<div class="grid-content bg-purple-dark">特色榜</div>
			<div style="width:1070px; border-bottom: 2px solid rgba(245,245,245);"></div>
		</el-row>
        <el-row style="margin-top: -8px;margin-left: 85px;">
			<!--特色榜只取所有榜单的前四个-->
			<!-- 以下方式显示出来的是所有榜单的内容 -->
			<!-- <el-col :span="6" v-for="item in topList" style="margin-top: 25px;">
			    <el-image class="el-image1" :src="item.coverImgUrl" @click="songList(item.id)"></el-image>
			</el-col> -->
			<!--API文档中coverImgUrl代表榜单的图片-->
            <el-col :span="6" v-for="i in 4" style="margin-top: 25px;">
                <el-image class="el-image1" :src="topList[i-1].coverImgUrl" @click="songList(topList[i-1].id)"></el-image>
				<!--tracks中只包含前3条数据,所以就只显示3条-->
				<el-table :data="topList[i-1] && topList[i-1].tracks"> 
					<!--表格序号-->
                    <el-table-column type="index" class="d1"></el-table-column>
					<!--tracks中有两个属性:first代表的是歌曲名，second代表的歌手名-->
                    <el-table-column property="first"></el-table-column>
					<el-table-column property="second"></el-table-column>
                </el-table>
				<p style="margin-left: 140px;font-weight: 200;font-size: 14px;cursor: pointer;" @click="songList(topList[i-1].id)">查看全部>></p>
            </el-col>
        </el-row>
		<!--全球榜-->
        <el-row class="el-row1">
        	<div class="grid-content bg-purple-dark">全球榜</div>
			<div style="width:1070px; border-bottom: 2px solid rgba(245,245,245);"></div>
        </el-row>	
        <el-row :gutter="10" style="margin-left: 85px;">
			<!--topList获取到的是全部的榜单信息,全球榜中要去掉前四个特色榜,通过slice()函数实现-->
            <el-col :span="6" v-for="(item,index) in topList.slice(4)" :key="index"
			style="margin-top: 20px;position:relative;">
                <div style="position: relative;">
					<!--获取全球榜的榜单图片-->
                    <el-image :src="item.coverImgUrl" @click="songList(item.id)"
                              style="box-shadow: 0 0 2px 2px gray;border-radius: 10px;cursor: pointer">
                    </el-image>
					<!--获取榜单信息中的播放量playCount-->
					<div style="position: absolute;top:0;margin-left: 90px;">
						<i class="el-icon-headset" style="margin-right: 5px;color: white;"></i>
						<span style="color: white;">{{item.playCount}}</span>
					</div>
                </div>
				<!--获取榜单的名字,同时绑定点击事件,点击后跳转到榜单详情页面-->
                <h4 @click="songList(item.id)" style="cursor:pointer;">{{item.name}}</h4>
            </el-col>
        </el-row>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                //存放获取到的所有榜单信息
                topList: [ {
                        coverImgUrl: ''
                    },
                    {
                        coverImgUrl: ''
                    },
                    {
                        coverImgUrl: ''
                    },
                    {
                        coverImgUrl: ''
                    },
				],
            }
        },
		/* vue实例处于某种特定的状态时，自动调用的方法 */
		/* created() 实例创建完成后执行的方法 页面加载完 */
		/*在这想要实现点击导航栏的排行榜后可以直接显示排行榜榜单信息*/
		created() {
            //获取所有榜单的数据
            this.getAllTopList()
        },
        methods: {
            //获得所有榜单的数据
            getAllTopList() {
				var that=this;
				//get请求
				this.$axios.get("https://autumnfish.cn/toplist/detail")
				.then((res)=>{
					//回调函数中，this的指向不是vue实例，而是当前的回调函数对象
					that.topList = res.data.list;
				},(err)=>{})
            },
            //跳转到榜单详细信息界面
            songList(id) {
				//在事件中利用$store.commit()调用mutations中的change方法
				//如果有参数 就在方法名后面加上,此处为id
				this.$store.commit("change",id);
				this.$router.push({
					//window.location.href=""
					name:"songList",
				});
            },
        },
    }
</script>

<style scoped>
/* 设置标题的底部边框和颜色 */
.el-row1{
	font-size: 25px;
	margin-left: 85px;
}
.el-image{
	height: 200px;
	border-radius: 15px;
	cursor:pointer;
}
.el-table {
    width: 220px;
	margin-top: -2px;
}
.d1{
	color: red;
}
</style>